'use client';

import React from 'react';
import { motion } from 'framer-motion';
import Link from 'next/link';
import { ArrowUpRight, Clock, MapPin } from 'lucide-react';
import { siteData, siteDataFr } from '@/data/siteData';

export const PopularDestinations = ({ locale, dict }: { locale: string, dict: any }) => {
    const data = locale === 'fr' ? siteDataFr : siteData;
    // Select the 3 featured tours
    const featuredTours = [
        data.tours.find(t => t.id === 'circuit-5-days-south'),
        data.tours.find(t => t.id === 'circuit-3-days-merzouga'),
        data.tours.find(t => t.id === 'circuit-8-days-imperial')
    ].filter(Boolean);

    return (
        <section className="py-20 container px-6 mx-auto md:px-4 ">
            {/* Minimalist Header */}
            <div className="text-center">
                <span className="text-primary text-xs font-bold uppercase tracking-[0.2em] mb-4 block">
                    {dict?.subtitle || "Curated Experiences"}
                </span>
                <h2 className="text-4xl md:text-5xl font-light text-neutral-dark font-poppins mb-6">
                    Signature <span className="font-serif italic text-primary">Journeys</span>
                </h2>
                <div className="w-24 h-1 bg-primary/20 mx-auto rounded-full"></div>
            </div>

            {/* Tours Grid */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                {featuredTours.map((tour: any, i: number) => (
                    <Link href={`/${locale}/tours/${tour.id}`} key={tour.id} className="block h-full">
                        <motion.div
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: i * 0.15, duration: 0.8 }}
                            className="group h-full flex flex-col"
                        >
                            {/* Image Card - Taller & Cleaner */}
                            <div className="relative h-[480px] rounded-[2rem] overflow-hidden mb-6 shadow-sm group-hover:shadow-xl transition-all duration-500">
                                <div
                                    className="absolute inset-0 bg-cover bg-center transition-transform duration-1000 group-hover:scale-110 ease-out"
                                    style={{ backgroundImage: `url(${tour.image})` }}
                                ></div>
                                <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-black/0 to-transparent opacity-60 group-hover:opacity-40 transition-opacity"></div>

                                <div className="absolute top-6 left-6 bg-white/10 backdrop-blur-md border border-white/20 text-white px-4 py-1.5 rounded-full text-xs font-bold uppercase tracking-wider">
                                    {tour.duration.replace('_', ' ')}
                                </div>

                                <div className="absolute bottom-8 left-8 right-8 text-white transform translate-y-4 group-hover:translate-y-0 transition-transform duration-500">
                                    <h3 className="text-2xl font-medium font-poppins leading-tight mb-2">
                                        {tour.name}
                                    </h3>
                                    <div className="opacity-0 group-hover:opacity-100 transition-opacity duration-500 flex items-center gap-2 text-sm font-light mt-4">
                                        <span>View Itinerary</span>
                                        <ArrowUpRight className="w-4 h-4" />
                                    </div>
                                </div>
                            </div>

                            {/* Minimalist Info below (Optional, or keep clean) */}
                            {/* Keep it clean: The info is inside the card on hover/static */}
                        </motion.div>
                    </Link>
                ))}
            </div>

            <div className="text-center mt-12">
                <Link
                    href={`/${locale}/tours`}
                    className="inline-flex items-center gap-2 text-neutral-dark hover:text-primary transition-colors font-medium text-sm tracking-wide border-b border-neutral-dark/20 hover:border-primary pb-1"
                >
                    View All Collections <ArrowUpRight className="w-4 h-4" />
                </Link>
            </div>
        </section>
    );
};
